@import url("https://use.typekit.net/gak5scy.css");
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");

.button-black {
  display: flex;
  background: #000;
  border-radius: 20px;
  text-transform: uppercase;
  border: none;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
}
.quiz-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 30px 0px;

  .quiz-icon {
    font-size: 34px;
  }
}
.products-list-border {
  margin: 30px 0px;
  border: 1px solid #fff;
}
@media screen and (min-width: 250px) and (max-width: 768px) {
  .button-black {
    padding: 6px 10px 6px 10px;
    font-size: 12px;
    font-weight: 400;
  }
  .button-white {
    padding: 6px 10px 6px 10px;
    font-size: 10px;
    font-weight: 400;
  }
}
@media screen and (min-width: 768px) and (max-width: 992px) {
  .button-black {
    padding: 6px 10px 6px 10px;
    font-size: 12px;
    font-weight: 400;
  }
  .button-white {
    padding: 6px 10px 6px 10px;
    font-size: 10px;
    font-weight: 400;
  }
}
@media screen and (min-width: 992px) {
  .button-black {
    padding: 10px;
    font-size: 12px;
    font-weight: 400;
  }
  .button-white {
    padding: 10px;
    font-size: 12px;
    font-weight: 400;
  }
  .quiz-section {
    font-size: 24px;
  }
}

.button-white {
  display: flex;
  background: #fff;
  border-radius: 20px;
  border: none;
  text-transform: uppercase;
  color: #000;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
}
.close-icon {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  cursor: pointer;
}
