              width: 70%;
              .product-card-feature {
                .product-card-feature-icon {
                  width: 30px;
                }
                .product-card-feature-name {
                  width: 100px;
                }
              }
            }
          }
        }
      }
    }
  }
  @media screen and (min-width: 1400px) {
    .products-list-body {
      margin: 30px;
      .products-list-heading {
        font-size: 34px;
      }
      .triple-dildo {
        .dildo-question {
          p {
            font-size: 32px;
          }

          span {
            font-size: 24px;
          }
        }
      }
      .product-list-swiper {
        .product-card {
          .product-card-top {
            .product-card-name {
