            }
            .product-card-price {
              font-size: 28px;
            }
          }
          .product-card-image {
            height: 300px;
            img {
              width: 60%;
            }
          }
          .product-card-bottom {
            .product-card-features {
              width: 70%;
              .product-card-feature {
                .product-card-feature-icon {
                  width: 30px;
                }
                .product-card-feature-name {
                  width: 100px;
                }
              }
            }
          }
        }
      }
    }
  }
}
